
import React from 'react';
import { Route } from 'react-router-dom';
import DataTable from '../../Forms/DataGrid';
import UnderWaterNew from '../../Forms/underWater/new'
import {AbForoushi,Ezafe,AdamNasb,Mansubat,EnteghalAb,KafShekani,TaghirKarbari,AdamTamdid,TaghirMahal,AbShirin,Plumb,Masloub} from '../../Forms/underWater/new/fields'
// import Profile from '../../Forms/Well/Profile';
// import Visit from '../../Forms/Well/Visit';
// import VisitWithoutProfile from '../../Forms/Well/VisitWithoutProfile';
// import Home from '../../Home';
// import Violation from '../../Forms/Well/Violation';

class Content extends React.Component {
    constructor(props){
        super();
        
    }
    render(){
        return (
            <div className="content-wrapper">
                <section className="content-header">
                    <div className="row">
                        <div className="col-md-12">
                            <div className="box">
                                <div className="box-header with-border">
                                    {/* <h3 className="box-title">Monthly Recap Report</h3> */}
                                </div>
                                <div className="box-body">
                                
                                    <div className="row">
                                        <div className="col-md-10">
                         
                                                    <Route path="/DataTableAbForoushi" component={() => <DataTable type='آب فروشی' fields={AbForoushi} table='AllowedViolations' />} />
                                                    <Route path="/DataTableEzafeh" component={() => <DataTable type='اضافه برداشت' fields={Ezafe} table='AllowedViolations'  />} />
                                                    <Route path="/DataTableAdamNasb" component={() => <DataTable type='عدم نصب' fields={AdamNasb} table='AllowedViolations' />} />
                                                    <Route path="/DataTableMansubat" component={() => <DataTable type='منصوبات' fields={Mansubat} table='AllowedViolations'/>} />
                                                    <Route path="/DataTableEnteghalAb" component={() => <DataTable type='انتقال آب' fields={EnteghalAb} table='AllowedViolations' />} />
                                                    <Route path="/DataTableKafShekani" component={() => <DataTable type='کف شکنی' fields={KafShekani}  table='AllowedViolations'/>} />
                                                    <Route path="/DataTableTaghirKarbari" component={() => <DataTable type='تغییر کاربری' fields={TaghirKarbari} table='AllowedViolations'/>} />
                                                    <Route path="/DataTableAdamTamdid" component={() => <DataTable type='عدم تمدید' fields={AdamTamdid} table='AllowedViolations'/>  }/>
                                                    <Route path="/DataTableTaghirMahal" component={() => <DataTable type='تغییر محل' fields={TaghirMahal} table='AllowedViolations'/>} />
                                                    <Route path="/DataTableAbShirin" component={() => <DataTable type='آب شیرین کن' fields={AbShirin} table='AllowedViolations'/>  }/>
                                                    
                                                    <Route path="/DataGridPlumb" component={()=><DataTable type='پلمپ چاههای غیر مجاز' fields={Plumb} table='NotAllowedViolations'/>  }/>
                                                    <Route path="/DataTableMasloub" component={()=><DataTable type='پر و مسلوب المنفعه بودن چاههای غیر مجاز' fields={Masloub} table='NotAllowedViolations'/>  }/>
                                                    <Route path="/UnderWaterNew" component={UnderWaterNew}  />
                                        </div>
                                    </div>
                                </div>
                               
                            </div>
                        </div>
                    </div>
                </section>
               
            </div>
        )
    }
}
export default Content